import ExtLink from "../ExtLink/ExtLink";
import React, { Component } from 'react';
import { Flex, Icon, Text, Image } from "rimble-ui";
import FunctionsUtil from '../utilities/FunctionsUtil';
import DashboardCard from "../DashboardCard/DashboardCard";

class FlashNews extends Component {

  state = {
    activeNews:null
  }

  // Utils
  functionsUtil = null;

  loadUtils(){
    if (this.functionsUtil){
      this.functionsUtil.setProps(this.props);
    } else {
      this.functionsUtil = new FunctionsUtil(this.props);
    }
  }

  async componentDidMount(){
    this.loadUtils();

    const gaugesConfg = this.functionsUtil.getGlobalConfig(["tools", "gauges"]);
    const stakingConfig = this.functionsUtil.getGlobalConfig(["tools", "stake"]);
    // const stakingPolygonConfig = this.functionsUtil.getGlobalConfig(["tools", "stakePolygon"]);

    const flashNews = {
      1: [
        {
          name: "Governance Forum",
          icon: "LightbulbOutline",
          text: `Do you have any idea to improve the Idle Protocol? Let's discuss it in our`,
          link: {
            text: `Governance Forum`,
            url: this.functionsUtil.getGlobalConfig(["forumURL"])
          }
        },
        {
          icon: stakingConfig.icon,
          name: stakingConfig.label,
          text: `You can now stake your $IDLE token and take part of the fee-sharing for long-term holders.`,
          link: {
            text: `Stake Now`,
            url: this.functionsUtil.getDashboardSectionUrl(`tools/${stakingConfig.route}`)
          }
        },
        {
          name: gaugesConfg.label,
          image: gaugesConfg.image,
          text: `stkIDLE Gauges are now live at full throttle with $IDLE rewards!`,
          link: {
            text: `Go to Gauges`,
            url: this.functionsUtil.getDashboardSectionUrl(`gauges`)
          }
        }
      ],
      137: [
        /*
        {
          icon: stakingPolygonConfig.icon,
          name: stakingPolygonConfig.label,
          text: `SushiSwap LP Staking on Polygon is now available!`,
          link: {
            text: `Stake Now`,
            url: this.functionsUtil.getDashboardSectionUrl(`tools/${stakingPolygonConfig.route}`)
          }
        }
        */
      ]
    };

    const currentNetworkId = this.functionsUtil.getRequiredNetworkId();
    const activeFlashNews = flashNews[currentNetworkId];
    const activeNews = activeFlashNews.length ? activeFlashNews[Math.floor(Math.random() * activeFlashNews.length)] : null;
    this.setState({
      activeNews
    });
  }

  async componentDidUpdate(prevProps,prevState){
    this.loadUtils();
  }

  render() {
    return this.state.activeNews ? (
      <DashboardCard
        cardProps={{
          p: 2,
          mb: 3,
          width: 1,
          backgroundColor:'flashBg'
        }}
        isActive={true}
        isInteractive={false}
      >
        <Flex
          alignItems={"center"}
          justifyContent={"center"}
          flexDirection={["column", "row"]}
        >
          {
            this.state.activeNews.icon ? (
              <Icon
                mr={1}
                size={"1.2em"}
                color={"flashColor"}
                name={this.state.activeNews.icon}
              />
            ) : this.state.activeNews.image && (
              <Image
                mr={1}
                width={"1.2em"}
                heght={"1.2em"}
                src={this.state.activeNews.image}
              />
            )
          }
          <Text
            fontSize={2}
            fontWeight={3}
            color={"flashColor"}
            textAlign={"center"}
          >
            {this.state.activeNews.text}
          </Text>
          <ExtLink
            ml={1}
            fontSize={2}
            fontWeight={3}
            color={"primary"}
            hoverColor={"primary"}
            href={this.state.activeNews.link.url}
            onClick={e =>
              this.functionsUtil.sendGoogleAnalyticsEvent({
                eventCategory: "UI",
                eventAction: "flashNews",
                eventLabel: this.state.activeNews.name
              })
            }
          >
            <Flex
              alignItems={"center"}
              flexDirection={"row"}
              justifyContent={"center"}
            >
              {this.state.activeNews.link.text}
              <Icon
                ml={1}
                size={"0.9em"}
                color={"primary"}
                name={"OpenInNew"}
              />
              .
            </Flex>
          </ExtLink>
        </Flex>
      </DashboardCard>
    ) : null;
  }
}

export default FlashNews;
